import React, { Children, cloneElement } from 'react';
import { element, elementType, node, number, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';

const CSS_PREFIX = 'vds-breadcrumb';
const CSS_STATE = 'vds-state';

const Breadcrumbs = ({ activeIndex, ariaLabel, children, className, tag: Tag, separator, ...remainingProps }) => {
  const selectedActiveIndex = activeIndex || Children.count(children) - 1;

  return (
    <Tag
      aria-label={ariaLabel}
      className={classnames(`${CSS_PREFIX}s-container`, className)}
      {...remainingProps}
    >
      <ol className={`${CSS_PREFIX}s`}>
        {
          Children.map(children, (child, index) => (
            <li
              aria-current={selectedActiveIndex === index ? "page" : undefined}
              className={classnames(CSS_PREFIX, {
                [`${CSS_STATE}--active`]: selectedActiveIndex === index
              })}
            >
              {
                index < Children.count(children) - 1
                  ? cloneElement(
                    child,
                    {
                      className: classnames(`${CSS_PREFIX}-link`, Children.only(child).props.className)
                    }
                  )
                  : child.props.children
              }
              {
                index < Children.count(children) - 1
                  ? cloneElement(
                    separator,
                    {
                      role: Children.only(separator).props.role || 'presentation'
                    }
                  )
                  : undefined
              }
            </li>
          ))
        }
      </ol>
    </Tag>
  );
};

Breadcrumbs.propTypes = {
  /**
   * Active index
   */
  activeIndex: number,

  /**
   * Aria label
   */
  ariaLabel: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Separator
   */
  separator: element,

  /**
  * Tag
  */
  tag: elementType
};

Breadcrumbs.defaultProps = {
  ariaLabel: 'Breadcrumb',
  separator: <Icon name="arrow-next" resolution="tiny" />,
  tag: 'nav'
};

Breadcrumbs.displayName = 'Breadcrumbs';

export default Breadcrumbs;
